
// The props are level and name. Level is an integer between 0 and 5
function Rating(props) {
    let rows = [];
    let maxLevel = 5;
    let steps = props.level;

    if (steps > maxLevel) {
        steps = maxLevel
    }

    for (let i = 0; i < steps; i++) {

        rows.push(
                <div className="step" style={{ backgroundColor: '#4B81BD', border: '2px solid #4B81BD' }}></div>
        );
    }

    if (steps < maxLevel) {
        for (let i = 0; i < maxLevel - steps; i++) {
            rows.push(<div className="step"></div>)
        }
    }

    return (
        <div className="steps">
            <div className="step-text">{props.name}</div>{rows}
        </div>
    );
}

export default Rating;